.container{
    display :inline-table;
    background-color: #F5F5F5;
    padding: 24px;
    border-radius: 2px;
    max-width: 279px;
}

.row1{
    display: flex;
    margin-bottom: 22px;
    align-items: center;
}

.info{
    flex-grow: 1;
}


.date{
    color: #8A8A8A;
    font-size: 14px;
    align-items: center;
    justify-content: flex-end;
}

.name{
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    letter-spacing: -0.0124em;
    color: #333333;
}

.position{
    font-size: 14px;
    color: #8A8A8A;
    margin-right: 141px;
}

.row2{
    width: 471px;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    color: #333333;
}